import { makeAutoObservable, toJS } from "mobx";
import { getWeb3 } from "./utils";
import HodlerAbi from "./abis/hodler";
import AcceleratorAbi from "./abis/accelerator";
import OptionsMarketAbi from "./abis/optionsMarket";
import PriceOracleAbi from "./abis/priceOracle";

const HodlerAddress = process.env.REACT_APP_HODLER_VAULT;
const AcceleratorAddress = process.env.REACT_APP_ACCELERATOR_VAULT;
const PriceOracleAddress = process.env.REACT_APP_PRICE_ORACLE;
const OptionsMarketAddress = process.env.REACT_APP_OPTIONS_MARKET_TOKEN;

// Model the application state.
class Vault {
    hodlerVault = {
        stakeDuration: false,
        notReadyTokens: false,
        tokens: false,
        availableOSM: false,
        maxOSM: false,
        burnPercentage: false,
        allowance: "0",
    };
    acceleratorVault = {
        purchaseFee: false,
        stakeDuration: false,
        notReadyTokens: false,
        tokens: false,
        maxEth: false,
        burnPercentage: false,
    };

    constructor() {
        makeAutoObservable(this);
    }
    fetchHodlerVault = async() => {
        let stakeDuration = 0;
        let notReadyTokens = 0;
        let tokens = 0;
        let availableOSM = 0;
        let maxOSM = 0;
        let burnPercentage = 0;
        let length = 0;
        let allowance = false;
        let stakeDurationDays = 0;
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const OptionsMarketTokenContract = await new web3.eth.Contract(
            OptionsMarketAbi,
            OptionsMarketAddress
        );
        const HodlerContract = await new web3.eth.Contract(
            HodlerAbi,
            HodlerAddress
        );
        try {
            stakeDuration = await HodlerContract.methods.getStakeDuration().call();
            stakeDurationDays = stakeDuration / 60 / 60 / 24;
        } catch (error) {
            console.info("HodlerContract.config", error);
        }
        try {
            allowance = await OptionsMarketTokenContract.methods
                .allowance(ethAddress[0], HodlerAddress)
                .call();
        } catch (error) {
            console.info("HodlerContract.config", error);
        }
        try {
            length = await HodlerContract.methods
                .lockedLPLength(ethAddress[0])
                .call();
        } catch (error) {
            console.info("lockedLPLength", error);
        }
        if (length !== "0") {
            try {
                for (let i = 0; i < length; i++) {
                    let data = await HodlerContract.methods
                        .getLockedLP(ethAddress[0], i)
                        .call();
                    if (+stakeDuration < new Date().getTime() / 1000 - data[2] &&
                        !data[3]
                    ) {
                        tokens = data[1];
                        tokens = +web3.utils.fromWei(tokens + "");
                        tokens = parseFloat(tokens).toFixed(4);
                        i = length;
                    } else {
                        tokens = 0;
                    }
                }
                for (let i = 0; i < length; i++) {
                    const lockedLP = await HodlerContract.methods
                        .getLockedLP(ethAddress[0], i)
                        .call();
                    if (!lockedLP[3]) {
                        notReadyTokens = web3.utils
                            .toBN(notReadyTokens)
                            .add(web3.utils.toBN(lockedLP[1]));
                    }
                }
                notReadyTokens -= tokens;
                notReadyTokens =
                    notReadyTokens !== 0 ? +web3.utils.fromWei(notReadyTokens + "") : 0;
                notReadyTokens = (notReadyTokens - tokens).toFixed(4);
            } catch (error) {
                console.info("getLockedLP", error);
            }
        }
        try {
            maxOSM = await HodlerContract.methods.maxTokensToInvest().call();
            maxOSM = web3.utils.fromWei(maxOSM);
        } catch (error) {
            console.info("HodlerContract.maxTokensToInvest", error);
        }
        try {
            availableOSM = await web3.eth.getBalance(HodlerAddress);
            availableOSM = web3.utils.fromWei(availableOSM);
            availableOSM = (+availableOSM).toFixed(4)
        } catch (error) {
            console.info("eth.getBalance", error);
        }
        this.hodlerVault = {
            stakeDuration: stakeDurationDays,
            notReadyTokens,
            tokens,
            availableOSM,
            maxOSM,
            burnPercentage,
            allowance,
        };
    };

    fetchAcceleratorVault = async() => {
        let stakeDuration = 0;
        let notReadyTokens = 0;
        let tokens = 0;
        let availableOSM = 0;
        let maxOSM = 0;
        let burnPercentage = 0;
        let length = 0;
        let purchaseFee = 0;
        let stakeDurationDays = 0;
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const OptionsMarketTokenContract = await new web3.eth.Contract(
            OptionsMarketAbi,
            OptionsMarketAddress
        );
        const PriceOracleContract = await new web3.eth.Contract(
            PriceOracleAbi,
            PriceOracleAddress
        );
        const AcceleratorContract = await new web3.eth.Contract(
            AcceleratorAbi,
            AcceleratorAddress
        );
        try {
            purchaseFee = await AcceleratorContract.methods.config().call();
        } catch (error) {}
        try {
            stakeDuration = await AcceleratorContract.methods
                .getStakeDuration()
                .call();
            stakeDurationDays = stakeDuration / 60 / 60 / 24;
        } catch (error) {
            console.info("AcceleratorContract.config", error);
        }
        try {
            length = await AcceleratorContract.methods
                .lockedLPLength(ethAddress[0])
                .call();
        } catch (error) {
            console.info("lockedLPLength", error);
        }

        if (length !== "0") {
            try {
                for (let i = 0; i < length; i++) {
                    let data = await AcceleratorContract.methods
                        .getLockedLP(ethAddress[0], i)
                        .call();
                    if (+stakeDuration < new Date().getTime() / 1000 - data[2] &&
                        !data[3]
                    ) {
                        tokens = data[1];
                        tokens = +web3.utils.fromWei(tokens + "");
                        tokens = parseFloat(tokens).toFixed(4);
                        i = length;
                    } else {
                        tokens = 0;
                    }
                }
                for (let i = 0; i < length; i++) {
                    const lockedLP = await AcceleratorContract.methods
                        .getLockedLP(ethAddress[0], i)
                        .call();
                    if (!lockedLP[3]) {
                        notReadyTokens = web3.utils
                            .toBN(notReadyTokens)
                            .add(web3.utils.toBN(lockedLP[1]));
                    }
                }
                notReadyTokens =
                    notReadyTokens !== 0 ? +web3.utils.fromWei(notReadyTokens + "") : 0;
                notReadyTokens = (notReadyTokens - tokens).toFixed(4);
            } catch (error) {
                console.info("getLockedLP", error);
            }
        }
        try {
            let availableOSM = await OptionsMarketTokenContract.methods
                .balanceOf(AcceleratorAddress)
                .call();
            availableOSM = web3.utils.fromWei(availableOSM + "");
            let consult = await PriceOracleContract.methods.consult().call();
            maxOSM = web3.utils.fromWei(consult * availableOSM + "");
            maxOSM = (+maxOSM).toFixed(4);
        } catch (error) {
            console.info("AcceleratorContract.maxTokensToInvest", error);
        }
        try {
            availableOSM = await OptionsMarketTokenContract.methods
                .balanceOf(AcceleratorAddress)
                .call();
            availableOSM = web3.utils.fromWei(web3.utils.toBN(availableOSM));
        } catch (error) {
            console.info("OptionsMarketTokenContract.balanceOf", error);
        }

        this.acceleratorVault = {
            stakeDuration: stakeDurationDays,
            notReadyTokens,
            tokens,
            availableOSM,
            maxOSM,
            burnPercentage,
            purchaseFee: purchaseFee.purchaseFee,
        };
    };

    purchaseLPHodler = async(value) => {
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const HodlerContract = await new web3.eth.Contract(
            HodlerAbi,
            HodlerAddress
        );

        try {
            await HodlerContract.methods
                .purchaseLP(web3.utils.toWei(value + ""))
                .send({ from: ethAddress[0] });
            this.fetchHodlerVault();
        } catch (error) {
            console.info("HodlerContract.purchaseLP", error);
        }
    };

    claimHodler = async() => {
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const HodlerContract = await new web3.eth.Contract(
            HodlerAbi,
            HodlerAddress
        );

        try {
            await HodlerContract.methods.claimLP().send({ from: ethAddress[0] });
            this.fetchHodlerVault();
        } catch (error) {
            console.info(error);
        }
    };
    purchaseLPAccelerator = async(value, balance) => {
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const AcceleratorContract = await new web3.eth.Contract(
            AcceleratorAbi,
            AcceleratorAddress
        );
        try {
            await AcceleratorContract.methods
                .purchaseLP()
                .send({ from: ethAddress[0], value: web3.utils.toWei(`${value}`) });
            this.fetchAcceleratorVault();
        } catch (error) {
            console.info("AcceleratorContract.purchaseLP", error);
        }
    };

    claimAccelerator = async() => {
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const AcceleratorContract = await new web3.eth.Contract(
            AcceleratorAbi,
            AcceleratorAddress
        );

        try {
            await AcceleratorContract.methods.claimLP().send({ from: ethAddress[0] });
            this.fetchAcceleratorVault();
        } catch (error) {
            console.info(error);
        }
    };

    hodlerApprove = async() => {
        const web3 = await getWeb3();
        const ethAddress = await web3.eth.getAccounts();
        const OptionsMarketTokenContract = await new web3.eth.Contract(
            OptionsMarketAbi,
            OptionsMarketAddress
        );
        await OptionsMarketTokenContract.methods
            .approve(
                HodlerAddress,
                "0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff"
            )
            .send({ from: ethAddress[0] });
        const allowance = await OptionsMarketTokenContract.methods
            .allowance(ethAddress[0], HodlerAddress)
            .call();
        localStorage.allowance = true;
        const newHodlerVault = Object.assign({}, this.hodlerVault);
        newHodlerVault.allowance = allowance;
        this.hodlerVault = newHodlerVault;
    };
    getHodlerBalance = () => toJS(this.hodlerVault);
    getAcceleratorBalance = () => toJS(this.acceleratorVault);
}

export const VaultStore = new Vault();